.burger{
width: 80%;
margin:auto;
height: 150px;
overflow:scroll;
text-align: center;
font-weight: bold;
font-size: 1.2rem;
box-sizing: border-box;
}



@media (min-width: 500px) and (min-height: 400px) {

  .burger{
width:350px;
height:300px;

  }

}




@media (min-width: 500px) and (min-height: 410px) {

  .burger{
width:450px;
height:400px;

  }

}







@media (min-width: 1000px) and (min-height: 700px) {

  .burger{
width:700px;
height:600px;

  }

}
